<template>

  <div
    class="ant-alert"
    :class="['ant-alert-'+type,{'ant-alert-with-description':description, 'ant-alert-no-icon':!showIcon}]"
    transition="bounce"
  >
    <i
      class="ant-alert-icon anticon"
      :class="'anticon-'+iconClass"
      v-if="showIcon"></i>
    <span class="ant-alert-message">{{message}}</span>
    <span class="ant-alert-description">{{description}}</span>
    <a
      class="ant-alert-close-icon"
      v-if="closable"
      @click="handleClose">
      {{closeText}}
      <i class="anticon anticon-cross" v-if="!closeText"></i>
    </a>
  </div>

</template>

<script>

/**
 * Alert 
 * @param type  successinfowarningerror
 * @param closable 
 * @param closeText 
 * @param message 
 * @param description 
 * @param onClose 
 * @param showIcon 
 */

export default {
  name: 'v-alert',
  props: {
    type: {
      type: String,
      require: true,
      default: 'info'
    },
    closable: {
      type: Boolean,
      default: false
    },
    closeText: {
      type: String,
      require: false
    },
    message: {
      type: String,
      require: true
    },
    description: {
      type: String,
      require: false
    },
    showIcon: {
      type: Boolean,
      default: false
    },
    onClose: {
      type: Function,
      require: false,
      default(){}
    }
  },
  data () {
    return {
      iconClass: '',
    }
  },
  computed: {
    iconClass(){
      var iconClass = ''
      switch(this.type){
        case 'success':
          iconClass = 'check-circle'
          break
        case 'info':
          iconClass = 'info-circle'
          break
        case 'warning':
          iconClass = 'exclamation-circle'
          break
        case 'error':
          iconClass = 'cross-circle'
          break
      }
      if (this.description) {
        iconClass += '-o'
      }
      return iconClass
    },
  },
  methods: {
    handleClose (e) {
      this.$remove()
      this.onClose()
    },
  }
}

</script>



